*
{
    margin:0px;
    padding: 0px;
    box-sizing: border-box;
}
.navbar{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    background-color: green;
    padding: 5px;
}
.navlinks{
    font-size: large;
    text-decoration: none;
    padding:10px;
    cursor: pointer;
    transition: ease-in-out;
}
.navlinks:hover{
    background-color: aqua;
}
.banner{
   text-align: center;
   background-image: url(/static/media/pexels-pixabay-164634.4b68f0c3.jpg);
   background-size: cover;
   background-position: center;
   height:100vh;
}
.content{
    background-color: rgb(209, 214, 202);
    width: 300px;
    border-radius: 25px;
    padding: 20px;
    position: relative;
    margin: 20px auto;
    align-items: center;
}
.content h3{
    font-size: 26px;
}

.filledData{
    margin-top: 100px;
}
.sections{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}
.sections div{
margin: 5px 10px;
}

table {
    width: 800px;
    height: 200px;
  }
  table, th, td {
    border: 1px solid black;
  }
    
  th {
    border-bottom: 1px solid black;
  }
    
  td {
    text-align: center;
  }
.membersContent{
    display: flex;
    align-items: center;
    justify-content: center;
}
.createForm{
    width: 300px;
    padding: 10px;
    border-radius: 25px;
    height: 100px;
    background-color: cadetblue;
    margin: 50px 60px;
    text-align: center;
}
.membersContent :hover{
    background-color: green;
}
.title{
    text-transform: uppercase;
    text-decoration: underline;
    color: blueviolet;
    margin-bottom: 5px;
}
.pagecontent{
    position: relative;
}
.pagecontent span{
    position: absolute;
    left: 0px;
}
