table
{
  width:40%;
  border: none;
}
td
{
  height:40px;
}


.formStatus{
  display: grid;
  grid-column-gap: 50px;
  grid-template-columns: repeat(3, auto);
  justify-content:space-between;
}
.formStatus div{
  margin: 20px;
  padding: 5px;
  cursor: pointer;
}
